import{
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useRouteMatch
} from 'react-router-dom';
import { useState } from 'react';
import './pages.scss';
import Modal from  '../component/Modal_categoriya';

import Group1 from '../assets/images/Group1.png';
import Group2 from '../assets/images/Group2.png';
import Group3 from '../assets/images/Group3.png';

import{
  HomePage,
  TopPage
}  from '../container';

import FooterCard from '../component/FooterCard'


const Home = () => {
  const {path, url } = useRouteMatch()
  const [activeClass, setActiveClass] = useState('')
  return (
    <>
        <div className='container'>
            <Modal/>
            <div className="home-links mb-4">
              <Link   className={`home-link ${activeClass == '' ? 'active' : ''}`} onClick={ () => setActiveClass('')}>Новые объявления</Link>
              <Link  className={`home-link ${activeClass == '/top' ? 'active' : ''}`} onClick={ () => setActiveClass('/top')}>Лучщие предложение</Link>
            </div>
            <div className={`home ${activeClass == '' ? 'active' : ''}`}>
                <HomePage/>
            </div>
            <div className={`home ${activeClass == '/top' ? 'active' : ''}`}>
                <TopPage/>
            </div>
        </div>
        <div className="carousel">
           <div className="carousel-inner container">
           <img className='carousel-img' src={Group1} alt=""/>
           <img className='carousel-img' src={Group2} alt=""/>
           <img className='carousel-img' src={Group3} alt=""/>
           <img className='carousel-img' src={Group1} alt=""/>
           <img className='carousel-img' src={Group2} alt=""/>
           <img className='carousel-img' src={Group3} alt=""/>
           </div>
        </div>

        <div className="footer-card container">
          <FooterCard/>
        </div>
    </>
  )
}

export default Home;