import React from "react";
import Carousel from "react-elastic-carousel";
import Item from "./Item";
import { useState } from 'react'
import {Link} from 'react-router-dom'
// import "./styles.css";

import './Modal.scss';
import Avto from '../../assets/images/icons/avto.svg';
import Uy from '../../assets/images/icons/turar.svg';
import Elektronika from '../../assets/images/icons/elektronika.svg';
import Ish from '../../assets/images/icons/ish.svg';
import Buyum from '../../assets/images/icons/buyum.svg';
import Sad from '../../assets/images/icons/sad.svg';
import Remont from '../../assets/images/icons/remont.svg';
import Sport from '../../assets/images/icons/sport.svg';
import Biznes from '../../assets/images/icons/biznes.svg';

import Arrow from  '../../assets/images/cardImages/arr.png';

const breakPoints = [
{ width: 1, itemsToShow: -19 },
{ width: 1, itemsToShow: 8 },
{ width: 1, itemsToShow: 9 },
{ width: 1, itemsToShow: 8.9 },
];



const Modal = () => {
  const [activeClass, setActiveClass] = useState(false)
  const [activeClassList, setActiveClassList] = useState(false)
  return (
    <>
  <div className={`box  ${activeClass? 'active' : ''}`}></div>

  <div className="App mb-5">
    <Carousel breakPoints={breakPoints}>
      <Item><img className='img-link' src={Avto} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      <Item><img className='img-link' src={Uy} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      <Item><img className='img-link' src={Elektronika} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      <Item><img className='img-link' src={Ish} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      <Item><img className='img-link' src={Buyum} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      <Item><img className='img-link' src={Sad} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      <Item><img className='img-link' src={Remont} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      <Item><img className='img-link' src={Sport} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      <Item><img className='img-link' src={Biznes} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      
      <Item><img className='img-link' src={Avto} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      <Item><img className='img-link' src={Uy} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      <Item><img className='img-link' src={Elektronika} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      <Item><img className='img-link' src={Ish} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      <Item><img className='img-link' src={Buyum} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      <Item><img className='img-link' src={Sad} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      <Item><img className='img-link' src={Remont} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      <Item><img className='img-link' src={Sport} alt="" onClick={ () => setActiveClass(!activeClass)} /></Item>
      

    </Carousel>
    <div className={`modal-dr ${activeClassList ? 'active' : ''}`}>
      <div class={`category-dropdown-menu category1  ${activeClass? 'active' : ''}`}><b class="icon icon-chevron-up dropdown-up"></b>
        <div class="category-dropdown-body full">
          <ul class="category-dropdown-item part25">
            <li><b class="icon icon-chevron-right"></b><a href="#"><span onClick={ () => setActiveClassList(!activeClassList)} >Телефоны</span></a></li>
            <li><b class="icon icon-chevron-right"></b><a href="#"><span onClick={ () => setActiveClassList(!activeClassList)}>Аудиотехника</span></a></li>
            <li><b class="icon icon-chevron-right"></b><a href="#"><span onClick={ () => setActiveClassList(!activeClassList)}>Климатическое оборудование</span></a></li>
          </ul>
          <ul class="category-dropdown-item part25">
            <li><b class="icon icon-chevron-right"></b><a href="#"><span onClick={ () => setActiveClassList(!activeClassList)}>Компьютеры</span></a></li>
            <li><b class="icon icon-chevron-right"></b><a href="#"><span onClick={ () => setActiveClassList(!activeClassList)}>Игры и игровые приставки</span></a></li>
            <li><b class="icon icon-chevron-right"></b><a href="#"><span onClick={ () => setActiveClassList(!activeClassList)}>Индивидуальный уход</span></a></li>
          </ul>
          <ul class="category-dropdown-item part25">
            <li><b class="icon icon-chevron-right"></b><a href="#"><span onClick={ () => setActiveClassList(!activeClassList)}>Фото / видео</span></a></li>
            <li><b class="icon icon-chevron-right"></b><a href="#"><span onClick={ () => setActiveClassList(!activeClassList)}>Техника для дома</span></a></li>
            <li><b class="icon icon-chevron-right"></b><a href="#"><span onClick={ () => setActiveClassList(!activeClassList)}>Аксессуары и комплектующие</span></a></li>
          </ul>
          <ul class="category-dropdown-item part25">
            <li><b class="icon icon-chevron-right"></b><a href="#"><span onClick={ () => setActiveClassList(!activeClassList)}>Тв / видеотехника</span></a></li>
            <li><b class="icon icon-chevron-right"></b><a href="#"><span onClick={ () => setActiveClassList(!activeClassList)}>Техника для кухни</span></a></li>
            <li><b class="icon icon-chevron-right"></b><a href="#"><span onClick={ () => setActiveClassList(!activeClassList)}>Прочая электроника</span></a></li>
          </ul>
        </div>
        
      </div>
    </div>

    <div className={`modal-drop ${activeClassList ? 'active' : ''}`}>
      <div class={`category-dropdown-menu category2  ${activeClassList? 'active' : ''}`}><b class="icon icon-chevron-up dropdown-up"></b>
      <div className="komp">
      <img src={Arrow} alt="Arrow" className='arrow-img' onClick={ () => setActiveClassList(!activeClassList)}/>
        <h2>Компьютеры </h2>
        <Link  className="modal-link"><h6>Посмотреть всю категорию компютеры</h6></Link>
      </div>
        <div class="category-dropdown-body full">
          <ul class="category-dropdown-item part25">
            <li><b class="icon icon-chevron-right"></b><a href="#" onClick={ () => setActiveClass(!activeClass)}><span onClick={ () => setActiveClassList(!activeClassList)}>Настольные</span></a></li>
            <li><b class="icon icon-chevron-right"></b><a href="#" onClick={ () => setActiveClass(!activeClass)}><span onClick={ () => setActiveClassList(!activeClassList)}>Ноутбуки </span></a></li>
            <li><b class="icon icon-chevron-right"></b><a href="#" onClick={ () => setActiveClass(!activeClass)}><span onClick={ () => setActiveClassList(!activeClassList)}>Планшетные компьютеры </span></a></li>
          </ul>
          <ul class="category-dropdown-item part25">
            <li><b class="icon icon-chevron-right"></b><a href="#" onClick={ () => setActiveClass(!activeClass)}><span onClick={ () => setActiveClassList(!activeClassList)}>Серверы</span></a></li>
            <li><b class="icon icon-chevron-right"></b><a href="#" onClick={ () => setActiveClass(!activeClass)}><span onClick={ () => setActiveClassList(!activeClassList)}>Комплектующие</span></a></li>
            <li><b class="icon icon-chevron-right"></b><a href="#" onClick={ () => setActiveClass(!activeClass)}><span onClick={ () => setActiveClassList(!activeClassList)}>Аксессуары уход</span></a></li>
          </ul>
 


          <ul class="category-dropdown-item part25">
            <li><b class="icon icon-chevron-right"></b><a href="#" onClick={ () => setActiveClass(!activeClass)}><span onClick={ () => setActiveClassList(!activeClassList)}>Периферийные устройства </span></a></li>
            <li><b class="icon icon-chevron-right"></b><a href="#" onClick={ () => setActiveClass(!activeClass)}><span onClick={ () => setActiveClassList(!activeClassList)}>Мониторы</span></a></li>
            <li><b class="icon icon-chevron-right"></b><a href="#" onClick={ () => setActiveClass(!activeClass)}><span onClick={ () => setActiveClassList(!activeClassList)}>Другое </span></a></li>
          </ul>
        </div>
      </div>
    </div>


  </div>
</>
)
}

export default Modal;