import FooterCard from './FooterCard';
import './main.scss';

import CardImg1 from '../../assets/images/HomeImg/f1.png';
import CardImg2 from '../../assets/images/HomeImg/f2.png';
import CardImg3 from '../../assets/images/HomeImg/f3.png';
import CardImg4 from '../../assets/images/HomeImg/f4.png';


const FooterCardItem = () => {

    const cardArr=[
        {
            img:CardImg1,
            title:'Светодиодная лампа с фронтальным стеклом 10шт',
            data:'Вчера 22:55',
            price:'39 000 000 сум'
        },
        {
            img:CardImg2,
            title:'Коттедж 424 м² на участке 22.6 сот.',
            data:'Вчера 22:55',
            price:'39 000 000 сум'
        },
        {
            img:CardImg3,
            title:'2-к квартира, 55 м², 3/5 эт.',
            data:'Вчера 22:55',
            price:'39 000 000 сум'
        },
        {
            img:CardImg4,
            title:'Кровать с закроватным модулем+ матрас Вита 160',
            data:'Вчера 22:55',
            price:'39 000 000 сум'
        },
    ]

    return(
       <>
         <div className="card-wrap">
            {
                cardArr.map(item => (
                    <FooterCard
                        Image={item.img}
                        title={item.title}
                        data={item.data}
                        price={item.price}
                    />
                ))
            }
        </div>
        <h5 className="bolshi">Покозать еще</h5>
       </>
    )
}

export default FooterCardItem;