import FooterCard from './FooterCard';
import './main.scss';

import CardImg1 from '../../assets/images/HomeImg/9.png';
import CardImg2 from '../../assets/images/HomeImg/10.png';
import CardImg3 from '../../assets/images/HomeImg/11.png';
import CardImg4 from '../../assets/images/HomeImg/12.png';


const FooterCardItem3 = () => {

    const cardArr=[
        {
            img:CardImg1,
            title:'iPhone 6/6+ 16/64/128 Гб (Магазин 1 год гарантии)',
            data:'Вчера 22:55',
            price:'39 000 000 сум'
        },
        {
            img:CardImg2,
            title:'Маркетолог. Консультирование. Настройка рекламы',
            data:'Вчера 22:55',
            price:'39 000 000 сум'
        },
        {
            img:CardImg3,
            title:'Требуется администратор в Отел',
            data:'Вчера 22:55',
            price:'39 000 000 сум'
        },
        {
            img:CardImg4,
            title:'Велосипед Merida Big Seven 70 D',
            data:'Вчера 22:55',
            price:'39 000 000 сум'
        },
    ]

    return(
       <>
         <div className="card-wrap">
            {
                cardArr.map(item => (
                    <FooterCard
                        Image={item.img}
                        title={item.title}
                        data={item.data}
                        price={item.price}
                    />
                ))
            }
        </div>
        <h5 className="bolshi">Покозать еще</h5>
       </>
    )
}

export default FooterCardItem3;