
import { Link } from 'react-router-dom';
import {useState} from 'react';
import Logo from '../../assets/images/icons/logo.svg';
import Plus from '../../assets/images/icons/+.svg';
import Plus2 from '../../assets/images/icons/plus.svg';
import Author from '../../assets/images/author.png';
import './Header.scss';

const HeaderReg = () => {
    const [hedActiveClass, setHedActiveClass] = useState(false)
    return ( 
       <div className="header-wrap">
           <div className="header container">
            <div className="header-top">
                <div className="header-top-left">
                    <Link to='/'>
                        <img src={Logo} alt=""/>
                    </Link>
                    <div className="header-text">
                        <h6>Продай, найди, купи <br/>
                            все что пожелаешь…
                        </h6>
                    </div>
                </div>

                <div className="header-top-middle">
                    <a href="#">Объявления</a>
                    <a href="#">Магазины </a>
                    <a href="#">Для бизнеса </a>
                    <a href="#">Помощь</a>
                </div>

                <div className="header-top-right">

                    <h5 className='lang'>Рус <span>|</span> O'z</h5>

                    <div className="login d-flex">

                        <Link to='/kabinet' className='author'>
                            <h3 className="author-name">Темиров</h3>
                            <img src={Author} alt="" className="author-img"/>
                        </Link>
                    </div>

                </div>
            </div>

            <div className={` header-bottom  ${hedActiveClass ? 'active' : ''}`}>
                <form className="header-bottom-left">
                    <div className="">
                    <select name="" id="">
                        <option value="">Любая категория</option>
                        <option value=""> Недвижимость</option>
                        <option value=""> Электроника</option>
                        <option value=""> Работа</option>
                    </select>

                    <input type="search" name="" id="" placeholder='Что будем искать ?'/>

                    <select name="" id="">
                        <option value="">По всей Узбекистану</option>
                        <option value=""> Ташкент</option>
                        <option value=""> Андижан</option>
                        <option value=""> Карши</option>
                    </select>
                    </div>
                    <Link to='/search'  ><button className='btn' onClick={ () => setHedActiveClass(!hedActiveClass)}>Найти</button> </Link>
                </form>
                <Link to='/add' className="header-bottom-right">
                    <button className='btn-add'> 
                    <div className="plus-wrap"><img src={Plus} alt="" className="plus"/></div>
                     <span className='add'>Добавить обьявления</span></button>
                </Link>
            </div>

            <div className="header-lich">
                <div className="top">
                    <div className="left">
                        <h3>Здравствуйте Зухриддин Темиров</h3>
                    </div>
                    <div className="right">
                        <h4>Ваш счет: 2 3000 сум</h4>
                        <button>Пополнить <span><img src={Plus2} alt=""/></span></button>
                    </div>
                </div>
            </div>
       </div>
       </div>
    )
}

export default HeaderReg;