import{
    Switch,
    Route,
    Link,
    useRouteMatch
} from 'react-router-dom';
import { useState } from 'react';

import './main.scss';
import FooterCardItem from './FooterCardItem';
import FooterCardItem2 from './FooterCardItem2';
import FooterCardItem3 from './FooterCardItem3';

const FooterCardAll = () => {
    const [activeClass, setActiveClass] = useState('')
    return(
        <>
            <div className="links-card mb-4">
                <Link  className={`link ${activeClass == '' ? 'active' : ''}`} onClick={ () => setActiveClass('')}>Просмотренные</Link>
                <Link  className={`link ${activeClass == 'ikki' ? 'active' : ''}`} onClick={ () => setActiveClass('ikki')}>Выбор редакции</Link>
                <Link  className={`link ${activeClass == 'uch' ? 'active' : ''}`} onClick={ () => setActiveClass('uch')}>Сниженные цены</Link>
            </div>

            <div className={`card-f ${activeClass == '' ? 'active' : ''}`}>
                 <FooterCardItem/>
            </div>

            <div className={`card-f ${activeClass == 'ikki' ? 'active' : ''}`}>
                <FooterCardItem2/>
            </div>

            <div className={`card-f ${activeClass == 'uch' ? 'active' : ''}`}>
                <FooterCardItem3/>
            </div>

        </>
    )
}

export default FooterCardAll;