
import './pages.scss';
import FormImg from '../assets/images/form.png';
import Kivi from '../assets/images/kivi.png';
import {useRef, useState} from 'react';
import request from '../services/http';

const Registr = ({setToken}) => {
    const emailInputRef = useRef(null);
    const passwordInputRef = useRef(null);
    const [ hasError, setHasError] = useState(false)

    const handleSubmit = (e) => {
        e.preventDefault();
        request.post('/login', {
            email: emailInputRef.current.value,
            password: passwordInputRef.current.value
        })
        .then( (response) => {
            window.localStorage.setItem('sessionToken', response.data.token);
            setToken(response.data.token)
        })
        .catch ( (err) => {
            setHasError(true)
        })
    }
    return(
        <div className="reg-wrap d-flex container">
            <div className="reg-wrap-left">
                <h2>Бесплатная регистрация</h2>
                {
                        hasError ? (
                            <div className="alert alert-danger">
                                Parol ili login oshibka
                            </div>
                        ) : (
                            <div className=""></div>
                        )
                    }
                     <form onSubmit={handleSubmit}  >
                        <div className="mb-3">
                            <label htmlFor="exampleInputEmail1" className="form-label">Введите ваш email</label>
                            <input ref={emailInputRef} type="email" className="form-control" id="exampleInputEmail1"
                                aria-describedby="emailHelp" placeholder='eve.holt@reqres.in'/>
                        </div>
                        <div className="mb-3">
                            <label htmlFor="exampleInputPassword1" className="form-label">Password</label>
                            <input ref={passwordInputRef} type="password" className="form-control" id="exampleInputPassword1" placeholder='Введите код'/>
                        </div>
                      
                        <button type="submit" className="btn btn-success" >Бесплатная регистрация</button>
                    </form>
                    <h4>или</h4>
                    <a href="#">Войдите в систему, если у вас уже есть аккаунт kivi.uz</a>
            </div>
            <div className="reg-wrap-right">
                <img src={FormImg} alt="" className="form-img"/>
                <div className="form-card">
                    <img src={Kivi} alt=""/>
                    <p>Откройте для себя 
                        целую плошадку
                        для продаже</p>
                    <button>Как это делать?</button>
                </div>
            </div>
        </div>
    )
}

export default Registr;