import{
  BrowserRouter as Router,
  Switch,
  Route
} from 'react-router-dom';
import {useState, lazy, Suspense} from 'react';

import './assets/styles/main.scss';
// import './assets/fonts/stylesheet.css';

import{
  Home,
  Card,
  Search,
  Add,
  Kabinet
} from './pages';

import Header from './container/Header';
import HeaderReg from './container/Header/HeaderReg';
import Footer from './component/Footer';
const RegistrPage = lazy( () => import('./pages/registr'));

function App() {
  const [token, setToken] = useState(window.localStorage.getItem('sessionToken'));
 
  return (
     <>
     <Suspense fallback={<h1>...Loding</h1>}>
          <Router>

            {
              token ? (
                <>
                
                <HeaderReg/>
                <Switch>
                  <Route exact path='/' component={Home}/>
                  <Route  path='/card' component={Card}/>
                  <Route  path='/search' component={Search}/>
                  {/* <Route  path='/login' render={ ()=> <RegistrPage setToken={setToken}/>}/> */}
                  <Route  path='/add' component={Add}/>
                  <Route  path='/kabinet' component={Kabinet}/>
                </Switch>
                <Footer/>
                </>
              ):(
                <> 
                <Header/>
                <Switch>
                  <Route exact path='/' component={Home}/>
                  <Route  path='/card' component={Card}/>
                  <Route  path='/search' component={Search}/>
                  <Route  path='/login' render={ ()=> <RegistrPage setToken={setToken}/>}/>
                  <Route  path='/add' component={Add}/>
                  <Route  path='/kabinet' component={Kabinet}/>
                </Switch>
                <Footer/>
                </>
              )
            }
          </Router>
     </Suspense>
     </>
  );
}

export default App;
